.main-footer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  background-color: #f1f1f1;
}

.main-footer__socials {
  padding: 0 80px;
}

.main-footer__developed {
  padding: 0 30px;
}

.main-footer__logo {
  display: none;
}

@media (min-width: $tablet-width) {
  .main-footer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    min-height: 143px;
    padding: 0 50px;
  }

  .main-footer__logo {
    display: block;
  }
}
